.auction__form {
  display: flex;
  align-items: center;
  flex-direction: column;
  max-width: 31.25rem;
  min-height: 4.625rem;
  margin: -2.875rem auto 7.5rem;
  padding: 0.875rem;
  border-radius: 0.75rem;
  background: #fff;
  box-shadow: 0 21.8125rem 6.125rem 0 rgba(0, 0, 0, 0),
  0 13.9375rem 5.5625rem 0 rgba(0, 0, 0, 0.01),
  0 7.8125rem 4.6875rem 0 rgba(0, 0, 0, 0.05),
  0 3.5rem 3.5rem 0 rgba(0, 0, 0, 0.09), 0 0.875rem 1.9375rem 0 rgba(0, 0, 0, 0.1);

  translate: 0 50%;
  input {
    height: 2.5rem;
    padding: 0 0.875rem;
    border: none;
  }
  button {
    width: 100%;
    margin-top: 1.25rem;
    @media (min-width: $mqsm) {
      flex: 1 0 7rem;
    }
  }
  @media (min-width: $mqsm) {
    flex-direction: row;
    margin-top: 0;
    button {
      margin: 0;
    }
  }
}
.auction__form--sign-up {
  max-width: 40rem;
  @media (min-width: $mqsm) {
    button {
      flex-basis: 10.9375rem;
    }
  }
}
.auction__form--bid {
  margin: 0;

  translate: none;
}
.auction__sign-up__divider {
  align-self: stretch;
  height: 0.0625rem;
  margin: 0.25rem 0;
  background: rgba(167, 167, 167, 0.2);
  @media (min-width: $mqsm) {
    flex: 0 0 0.0625rem;
    width: 0.0625rem;
    height: auto;
    margin: 0 0.875rem;
  }
}
.auction__table {
  overflow: hidden;
  margin-top: -2.5rem;
  @media (min-width: $mqsm) {
    margin-top: -5rem;
  }
}
.auction__grid {
  margin: 3.75rem auto 2.5rem;
  padding-bottom: 3.125rem;
  text-align: center;


  @media (min-width: $mqsm) {
    // margin: -2.875rem auto 7.5rem;
    margin: 0 auto 7.5rem;
    padding-bottom: 0;

    translate: 0 50%;
  }
}
.auction__wrapper {
  display: inline-flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
}
.auction__time {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.5rem;
  min-width: 14.375rem;
  min-height: 4.875rem;
  border-radius: 0.75rem;
  background: #fd064a;
  box-shadow: 0 21.8125rem 6.125rem 0 rgba(0, 0, 0, 0.00), 0 13.9375rem 5.5625rem 0 rgba(0, 0, 0, 0.01), 0 7.8125rem 4.6875rem 0 rgba(0, 0, 0, 0.05), 0 3.5rem 3.5rem 0 rgba(0, 0, 0, 0.09), 0 0.875rem 1.9375rem 0 rgba(0, 0, 0, 0.10);

  padding-inline: 1.375rem;
}
.auction__time__label {
  color: #fff;
  font-size: 1rem;
  font-weight: 400;
  line-height: normal;
}
.auction__time__value {
  color: #fff;
  font-size: 1rem;
  font-weight: 700;
  line-height: normal;
}
