.custom-select {
  position: relative;
  display: inline-block;
  width: 100%;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  max-width: 100%;
  background: #fff;
  color: #2c2e3f;
  font-size: 16px;
  line-height: 1.25;
}
.custom-select select {
  display: none;
}
.custom-select:before {
  position: absolute;
  content: "";
  top: 50%;
  right: 7px;
  transform: translate(0, -50%) scale(1, 1);
  transition: all 300ms ease;
  pointer-events: none;
  width: 14px;
  height: 9px;
  z-index: 21;
  background: url(../img/dropdown.svg) no-repeat;
}
.custom-select.select-arrow-active {
  z-index: 22;
}
.custom-select.select-arrow-active:before {
  transform: translate(0, -50%) scale(-1, -1);
}
.custom-select.is-danger .select-selected {
  border-color: #c81917;
  color: #c81917;
}

.custom-select--clear .select-selected {
